import { useForm } from 'react-hook-form';
import { Form } from '../form';
import { FormButton } from '../form-button';
import { FormInput } from '../form-input';


export function ResetPassword({ onSubmit }) {
    const {
        register,
        handleSubmit,
        formState: { errors }
    } = useForm({ mode: "onBlur" })

    const emailRegister = register('email', {
        required: {
            value: true,
            message: ' '
        },
        pattern: {
            value: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
            message: 'E-mail     '
        }
    })

    return (
        <Form title=' ' handleFormSubmit={handleSubmit(onSubmit)}>
            <p className='message__info'>   
                  email,   .</p>

            <FormInput
                {...emailRegister}
                id='email'
                type='text'
                placeholder='email'
            />
            {errors?.email &&
                <p className='message__error'>{errors?.email?.message}</p>
            }

            <p className='message__info'>    24 .</p>

            <FormButton type='submit' color='pramary'></FormButton>
        </Form>

    );
}
