import { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Form } from '../form';
import { FormInput } from '../form-input';
import { FormButton } from '../form-button';
import { useLocation, useNavigate } from 'react-router-dom';

export function Register({ onSubmit, onNavigateLogin }) {
    const { register, handleSubmit, formState: { errors } } = useForm({ mode: 'onBlur' })

    const emailRegister = register('email', {
        required: {
            value: true,
            message: ' '
        },
        pattern: {
            value: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
            message: 'E-mail     '
        }
    })

    const passwordRegister = register('password', {
        required: {
            value: true,
            message: ' '
        },
        pattern: {
            value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
            message: `     ,  
                             `
        }
    })
    return (

        <Form title='' handleFormSubmit={handleSubmit(onSubmit)}>
            <FormInput
                {...emailRegister}
                id='email'
                type='text'
                placeholder='E-mail'
            />

            {errors?.email &&
                <p className='message__error'>{errors?.email?.message}</p>
            }

            <FormInput
                {...passwordRegister}
                id='password'
                type='password'
                placeholder=''
            />

            {errors?.password &&
                <p className='message__error'>{errors?.password?.message}</p>
            }

            <p className='message__info'>  ,  
                      
                   .</p>

            <FormButton
                type='submit'
                color='pramary'
            ></FormButton>
            <FormButton
                type='button'
                color='secondary'
                onClick={onNavigateLogin}
            ></FormButton>
        </Form>

    );
}
